import React from 'react';
import avatar from '../imgs/papi.jpeg'


const Header = () => {
   return (
      <header className="masthead text-white text-center">
         <div className="container d-flex align-items-center flex-column">

            <img className="masthead-avatar mb-5" src={avatar} alt="..." />
            <h1 className="masthead-heading text-uppercase mb-0">James Gonzales</h1>
            <div className="divider-custom divider-light">
               <div className="divider-custom-line"></div>
               <div className="divider-custom-line"></div>
            </div>
            <p className="masthead-subheading font-weight-light mb-0">Artist - Web Designer - Barber Enthueist</p>
         </div>
      </header>

   )
}

export default Header;